@import './variables/variable.scss';

.Sub{
    float:right;
    width:calc(100vw - 80px);
    padding-top:50px;
    text-align: left;
    @include mobile{
        width:100vw;
    }
    section{
        margin-bottom: 4em;
        padding:0 1.5em;

    }
    .subVisual{
        width:100%;
        height:14em;
        display: flex;
        position:relative;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-image: url(../../public/images/main/main_visual_bg.png);
        background-size: cover;
        background-position: 50% 50%;
        .title{
            font-weight: 500;
            color:#fff;
            margin: 0;
            font-size: 2em;
        }
    }
    .tabArea{
        margin:2em 3.5em 0;
        box-sizing: border-box;  
        overflow: hidden;
        @include mobile{
            font-size: 0.8em;
        }
    .tabWrap{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        position: relative;
        left:0px;
        &:after{
            position: absolute;
            width:100%;
            height:2px;
            content: '';
            bottom:0;
            left:0;
            background-color: rgb(231, 231, 231);
            z-index: -1;
        }
        .tab{
            padding:0.5em 1em;
            position: relative;
            font-weight: 600;
            flex-shrink: 0;
            &.on{
                &:after{
                    position: absolute;
                    width:100%;
                    height:2px;
                    content: '';
                    bottom:0;
                    left:50%;
                    transform: translateX(-50%);
                    background-color: #003F74;
                }
            }

            
        }
    }
    }
    .sub{
        padding:2em 3.5em 0;
        box-sizing: border-box;
        display: none;
        &.on{
            display: block;
        }
        @include small{
            padding: 2em 1.5em 0;
        }
        .titleArea{
            margin-bottom: 0.8em;
            .mainTitleWrap{
                position: relative;
                margin-bottom: 0.7em;
                &:after{
                    width:100%;
                    height:2px;
                    background-color: #003F74;
                    position: absolute;
                    top:50%;
                    left:0;
                    content: '';
                    z-index: -1;
                    transform: translateY(-50%);
                }
                .mainTitle{
                    font-size: 1.75em;
                    font-weight: 600;
                    background-color: #fff;
                    padding-right: 0.5em;
                    display: inline-flex;
                    margin: 0;
                }
            }
            .subTitleWrap{
                .subTitle{
                    font-weight: 400;
                }

            }
        }
        .contextArea{
            display: flex;
            
            .imageWrap{
                width:15em;
                height:20em;
                flex-shrink: 0;
                margin-right: 1em;
                background-color: rgb(224, 224, 224);
            }
            .textWrap{
                p{
                    line-height: 1.4;
                }
            }
            @include mobile{
                flex-direction: column;
                .imageWrap{
                    width: 100%;
                    margin-bottom: 1em;
                }
            }
        }
    }

}